import { ElButton, ElMessage, ElInput } from "element-plus";
export default {
  components: {
    ElButton,
    ElInput
  },
  data() {
    return {
      currentTime: "",
      xinzuo: "",
      desc: "我需要你根据今天的日期和我的星座进行分析，尽量详细=>",
      func: "",
      loading: false,
      showPopup: false,
      selectedZodiac: "",
      selectedFortuneType: ""
    };
  },
  created() {
    // 在组件创建时获取当前日期
    this.getCurrentDate();
  },
  methods: {
    // 获取当前日期
    getCurrentDate() {
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      this.currentTime = `${year}-${month}-${day}`;
    },
    // 显示运势选择弹窗
    showFortunePicker(zodiacName) {
      this.selectedZodiac = zodiacName;
      this.showPopup = true;
    },
    // 关闭弹窗
    closePopup() {
      this.showPopup = false;
    },
    // 获取星座图片名称
    getZodiacImage() {
      const imageMap = {
        '白羊座': 'baiyang',
        '金牛座': 'jinniu',
        '双子座': 'shuangzi',
        '巨蟹座': 'juxie',
        '狮子座': 'shizi',
        '处女座': 'chunv',
        '天秤座': 'tianping',
        '天蝎座': 'tianxie',
        '射手座': 'sheshou',
        '摩羯座': 'mojie',
        '水瓶座': 'shuiping',
        '双鱼座': 'shuanyv'
      };
      return imageMap[this.selectedZodiac] || '';
    },
    // 选择运势类型
    selectFortuneType(fortuneType) {
      this.func = fortuneType;
      this.xinzuo = this.selectedZodiac;
      this.closePopup();
      this.generateAstro();
    },
    // 生成星座运势并跳转
    async generateAstro() {
      this.loading = true;
      try {
        let msg = this.desc + "星座:" + this.xinzuo + ",今天日期：" + this.currentTime + ",需要分析的内容是：" + this.func; // 获取当期的日期 + 星座 + 具备详细星座分析的大模型文案
        const encodedDescription = encodeURIComponent(msg);
        try {
          const newWindow = window.open(`/#/chat?user_input=${encodedDescription}`);
          if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
            window.location.href = `/#/chat?user_input=${encodedDescription}`;
          }
        } catch (error) {
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        }
      } catch (error) {
        ElMessage.error('生成星座运势信息时出错');
        console.error(error);
      } finally {
        this.loading = false;
      }
    }
  }
};